export class JobAnalysisReportViewController extends ReportBaseViewController {
	constructor( options = {} ) {
		_.defaults( options, {} );

		super( options );
	}

	initReport( options ) {
		this.script_name = 'JobDetailReport';
		this.viewId = 'JobAnalysisReport';
		this.context_menu_name = $.i18n._( 'Job Analysis' );
		this.navigation_label = $.i18n._( 'Saved Report' );
		this.view_file = 'JobAnalysisReportView.html';
		this.api = TTAPI.APIJobDetailReport;
	}

	getCustomContextMenuModel() {
		return { include: ['default'] };
	}

}